	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"It is all to easy to dismiss problematic codebases on some nebulous idea of bad practice or bad programmers. Poor code, however, is rarely arbitrary and random in its structure or formulation. Systems of code, well or poorly structured, emerge from systems of practice, whether effective or ineffective. To improve code quality, it makes more sense to pick apart the specific practices and see their interplay — the cause — than to simply focus on the code itself — the effect. This talk looks at how a handful of coding habits, design practices and assumptions can systematically balloon code and compound its accidental complexity.","meta":{"duration":"PT1H11M23S"},"presenter":{"name":"Kevlin Henney"},"source":{"id":"FyCYva9DhsI","provider":"youtube"},"slug":"clean-coders-hate-what-happens-to-your-code-when-you-use-these-enterprise-programming-tricks","tag":["NDC Conferences","Clean Code"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/FyCYva9DhsI/default.jpg","height":90,"width":120}},"title":"Clean Coders Hate What Happens to Your Code When You Use These Enterprise Programming Tricks"}},{"node":{"description":"How did our industry start, what paths did it take to get to where we are, and where is it going. What big problems did programmers encounter in the past? How were they solved? And how do those solutions impact our future? What mistakes have we made as a profession; and how are we going to correct them. In this talk, Uncle Bob describes the history of software, from it’s beginnings in 1948 up through the current day; and then beyond. By looking at our past trajectory, we try to plot out where our profession is headed, and what challenges we’ll face along the way.\n\nRobert C. Martin (Uncle Bob) has been a programmer since 1970. He is the Master Craftsman at 8th Light inc, an acclaimed speaker at conferences worldwide, and the author of many books including: The Clean Coder, Clean Code, Agile Software Development: Principles, Patterns, and Practices, and UML for Java Programmers.","meta":{"duration":"PT1H18M21S"},"presenter":{"name":"\"Uncle Bob\" - Robert Cecil Martin"},"source":{"id":"ecIWPzGEbFc","provider":"youtube"},"slug":"the-future-of-programming","tag":["Clean Code"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/ecIWPzGEbFc/default.jpg","height":90,"width":120}},"title":"The Future of Programming"}}]}},"pathContext":{"slug":"clean-code","tag":"Clean Code"}}

